}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.flex-vc{
  display: flex;
  align-items: center;
}
.content-box {
  flex: 1 0 auto;
}
#footer {
  flex: 0 0 auto;
}
.container-fluid{
  max-width: 1400px;
}
</style>



